.container {
    width: 300px;
    display: flex;
}

.content {
    z-index: var(--layer-item-base);
    width: 100%;
    display: flex;
    align-items: center;
}

.rewards {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    font-weight: 700;
    margin-left: 15px;
    text-shadow: 1px 1px rgba(51, 51, 51, .5);
}

.rewardImage {
    margin: 0px 0px 0px auto;
    width: 100%;
    object-fit: contain;
}

.fadeOut  {
    animation-name: fadeOut;
    animation-duration: 1.0s;
    opacity: 0;
}

.imageFadeInAndOut  {
    margin: 0px 0px 0px auto;
    width: 100%;
    object-fit: contain;
    animation-name: fadeInAndOut;
    animation-duration: 5.0s;
    opacity: 0;
}

.progressBarLeft {
    position: absolute;
    left: 0px;
    height: 100%;
    z-index: var(--layer-item-progress);
    transition: width 0.5s;
}

.progressBarRight {
    position: absolute;
    right: 0px;
    height: 100%;
    opacity: 0.5;
    background-color: #ffffff;
    z-index: var(--layer-item-progress);
    transition: width 0.5s;
}

.flash {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: var(--layer-item-flash);
    animation-name: blink;
    animation-duration: 1.0s;
    animation-delay: 0.5s;
    animation-iteration-count: 5;
    background-color: #ffffff;
    opacity: 0;
}


@keyframes fadeInAndOut {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
	}
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
	}
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
	}
}

@keyframes blink {
	0% {
        opacity: 0;
	}
	50% {
        opacity: 1;
	}
	50% {
        opacity: 1;
    }
	100% {
        opacity: 0;
    }
}
